"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { getWikiArticles } from "@/lib/storage"
import { Search, Eye, Heart, BookOpen } from "lucide-react"
import Link from "next/link"

export default function WikiPage() {
  const [searchQuery, setSearchQuery] = useState("")
  const articles = getWikiArticles()

  const filteredArticles = articles.filter((article) => {
    if (!searchQuery.trim()) return true
    
    const query = searchQuery.toLowerCase().trim()
    const titleMatch = article.title?.toLowerCase().includes(query) ?? false
    const contentMatch = article.content?.toLowerCase().includes(query) ?? false
    const categoryMatch = article.category?.toLowerCase().includes(query) ?? false
    
    return titleMatch || contentMatch || categoryMatch
  })

  const categories = [
    { value: "all", label: "All Articles", icon: BookOpen },
    { value: "care", label: "Care", icon: Heart },
    { value: "health", label: "Health", icon: Heart },
    { value: "training", label: "Training", icon: BookOpen },
    { value: "nutrition", label: "Nutrition", icon: BookOpen },
    { value: "behavior", label: "Behavior", icon: BookOpen },
    { value: "breeds", label: "Breeds", icon: BookOpen },
  ]

  const getArticlesByCategory = (category: string) => {
    if (category === "all") return filteredArticles
    return filteredArticles.filter((article) => article.category === category)
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-7xl">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-2">Pet Care Wiki</h1>
        <p className="text-muted-foreground text-lg">
          Your comprehensive guide to pet care, health, training, and more
        </p>
      </div>

      <div className="mb-6">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            type="search"
            placeholder="Search articles..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-10"
          />
        </div>
      </div>

      <Tabs defaultValue="all" className="w-full">
        <TabsList className="grid w-full grid-cols-3 lg:grid-cols-7 mb-6">
          {categories.map((category) => (
            <TabsTrigger key={category.value} value={category.value}>
              {category.label}
            </TabsTrigger>
          ))}
        </TabsList>

        {categories.map((category) => (
          <TabsContent key={category.value} value={category.value}>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {getArticlesByCategory(category.value).map((article) => (
                <Link key={article.id} href={`/wiki/${article.slug}`}>
                  <Card className="hover:shadow-lg transition-shadow cursor-pointer h-full">
                    {article.coverImage && (
                      <div className="aspect-video w-full overflow-hidden rounded-t-lg">
                        <img
                          src={article.coverImage || "/placeholder.svg"}
                          alt={article.title}
                          className="w-full h-full object-cover"
                        />
                      </div>
                    )}
                    <CardHeader>
                      <div className="flex items-start justify-between gap-2 mb-2">
                        <Badge variant="secondary" className="capitalize">
                          {article.category}
                        </Badge>
                        {article.species && article.species.length > 0 && (
                          <Badge variant="outline" className="capitalize">
                            {article.species[0]}
                          </Badge>
                        )}
                      </div>
                      <CardTitle className="line-clamp-2">{article.title}</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <p className="text-sm text-muted-foreground line-clamp-3">
                        {article.content.substring(0, 150)}...
                      </p>
                      <div className="flex items-center gap-4 mt-4 text-sm text-muted-foreground">
                        <div className="flex items-center gap-1">
                          <Eye className="h-4 w-4" />
                          {article.views}
                        </div>
                        <div className="flex items-center gap-1">
                          <Heart className="h-4 w-4" />
                          {article.likes.length}
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </Link>
              ))}
            </div>
            {getArticlesByCategory(category.value).length === 0 && (
              <Card>
                <CardContent className="p-8 text-center text-muted-foreground">
                  No articles found in this category
                </CardContent>
              </Card>
            )}
          </TabsContent>
        ))}
      </Tabs>
    </div>
  )
}
